.stand {
  display: flex;
  justify-content: flex-start; /* не нужно space-between */
  align-items: flex-start;
  gap: 132px; /* горизонтальный отступ между инпутами и кнопками */
  margin-top: 90px; /* отступ сверху для всего блока */
  padding-bottom: 55px; /* отступ снизу под кнопку "Дополнительная" */
}

.inputs {
  display: flex;
  flex-direction: column;
  gap: 20px; /* расстояние между двумя инпутами */
}

.buttons {
  display: flex;
  flex-direction: column;
  gap: 15px; /* расстояние между кнопками */
}

.checkboxes {
  display: flex;
  flex-direction: column;
  gap: 30px; /* расстояние между чекбоксами */
  justify-content: center;
}

/* стили для чекбоксов */
.checkbox {
  appearance: none;
  width: 24px; /* ширина по макету */
  height: 24px; /* высота по макету */
  border-radius: 3px;
  border: 2px solid #06c;
  cursor: pointer;
  position: relative;
}

.checkbox:checked {
  appearance: none;
  background-color: transparent;
}

/* встроенная галочка через ::after с SVG-путьом */
.checkbox:checked::after {
  content: '';
  position: absolute;
  top: 3px;
  left: 5px;
  width: 10px;
  height: 9px;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="10" height="9" viewBox="0 0 10 9"><path d="M7.70019 1.38993L3.92019 6.38993L2.29019 4.27993C2.12708 4.07041 1.88742 3.93427 1.62393 3.90145C1.36044 3.86863 1.09471 3.94182 0.885189 4.10493C0.675668 4.26804 0.539523 4.5077 0.506704 4.77119C0.473885 5.03468 0.547081 5.30041 0.710189 5.50993L3.14019 8.61993C3.23436 8.73911 3.35445 8.83527 3.49133 8.90111C3.62821 8.96696 3.7783 9.00075 3.93019 8.99993C4.08291 8.99957 4.23351 8.96422 4.37045 8.89662C4.50739 8.82902 4.62703 8.73095 4.72019 8.60993L9.29019 2.60993C9.45197 2.39776 9.52284 2.13001 9.48721 1.86558C9.45158 1.60116 9.31236 1.36171 9.10019 1.19993C8.88802 1.03815 8.62027 0.96728 8.35584 1.00291C8.09141 1.03854 7.85197 1.17776 7.69019 1.38993H7.70019Z" fill="%230066CC"/></svg>');
  background-size: contain;
  background-repeat: no-repeat;
}

.checkbox:focus {
  border-color: #0059b3;
}
