.inputAndButtonContainer {
  display: flex;
  align-items: flex-start; /* чтобы кнопка оставалась на линии с инпутом */
  gap: 10px;
}

.inputWrapper {
  display: flex;
  flex-direction: column; /* инпут сверху, ошибка снизу */
}

.input {
  width: 372px;
  height: 36px;
  padding: 0 10px; /* только горизонтальные паддинги */
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  outline: none;
  box-sizing: border-box;
}

.input:focus {
  border-color: rgba(0, 102, 204, 1);
}

.errorInput {
  border-color: rgba(204, 0, 0, 1);
}

.error {
  color: rgba(204, 0, 0, 1);
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin-top: 5px;
}

.button {
  height: 36px; /* совпадает с инпутом */
  padding: 0 24px; /* только горизонтальные */
  background: rgba(0, 102, 204, 1);
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  opacity: 0.5;
  transition: opacity 0.2s;
}

.button:focus,
.button:active {
  outline: none;
  opacity: 1;
}

.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.line {
  width: 100%;
  height: 1px;
  background: #d9d9d9;
}

/* контейнер для статического уведомления "Людей добавили" */
.notification {
  margin-bottom: 9px; /* отступ от инпута */
}

/* контейнер для новых сообщений (например, приветствие последнего пользователя) */
.newMessages {
  margin-top: 24px; /* отступ от инпута */
}

/* общий контейнер формы */
.greetingForm {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  gap: 10px;
}

/* приветствие последнего пользователя */
.greeting {
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 500;
}
